





























































import {Component, Prop, Vue} from 'vue-property-decorator';
import SmallPage from './SmallPage.vue';
import PageHeader from './PageHeader.vue';
import PageBody from './PageBody.vue';
import PageFooter from './PageFooter.vue';
import { ArrowRightSmallIcon } from './Icons';

@Component({components: {SmallPage, PageHeader, PageBody, PageFooter, ArrowRightSmallIcon}})
export default class MigrationWelcome extends Vue {
    @Prop({type: String, default: 'https://medium.com/nimiq-network'}) public link!: string;

    private page = 1;

    private content = [{
        title: 'One Account, many Addresses',
        notice: 'The new and improved Accounts<br>can have multiple Addresses.',
        button: 'Continue',
    }, {
        title: 'Improved Accessibility',
        notice: 'Log in on your devices with the new<br>Nimiq Login Files.',
        button: 'What else?',
    }, {
        title: 'Old and new Accounts',
        notice: 'Multiple Addresses and Login Files<br>are supported by new Accounts only.',
        button: 'Got it!',
    }];

    public reset() {
        this.page = 1;
    }

    private buttonClick() {
        if (this.page === 3) this.$emit('finished');
        else this.page += 1;
    }

    private get title() {
        return this.content[this.page - 1].title;
    }

    private get notice() {
        return this.content[this.page - 1].notice;
    }

    private get buttonText() {
        return this.content[this.page - 1].button;
    }
}
